import scaling from '@/utils/scaling.js'
export default {
  data() {
    return {
      scaling: {
        zoom: 1,
        sw: 0,
        sh: 0,
        top: 0,
        left: 0,
        transformX:0
      }
    }
  },
  methods: {
    handleResize() {
      const _scale = scaling(1920, 1080, this.$el.offsetWidth, this.$el.offsetHeight)
      this.scaling.sw = this.$el.offsetWidth
      this.scaling.sh = this.$el.offsetHeight
      this.scaling.zoom = _scale.scale
      this.scaling.top = _scale.top ? _scale.top/2: 0
      this.scaling.left = _scale.left ? _scale.left/2: 0
      this.$emit('resize', this.scaling)
    }
  },
  mounted() {
    this.handleResize()
    window.addEventListener('resize', this.handleResize)
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.handleResize)
  }

}
