<template>
  <div class="LeftMidOne">
    <div class="LeftMidOne-ul">
      <div>
        <span class="font21">{{HospitalCount | format}}</span>
        <span class="font12">接入医疗机构</span>
        <span class="font12">(家)</span>
      </div>
      <div>
        <span class="font21">{{VisitCount | format}}</span>
        <span class="font12">就诊人数</span>
        <span class="font12">(人次)</span>
      </div>
      <div>
        <span class="font21">{{CreditCount | format}}</span>
        <span class="font12">先看病后付款</span>
        <span class="font12">(人次)</span>
      </div>
    </div>
    <!-- <div class="LeftMidOne-mid">
      <span v-if="judegeMoney==true" class="font14 colorBlue">累计履约金额（元）</span>
      <span v-else class="font14 colorBlue">累计履约金额（万元）</span>
      <span v-if="judegeMoney==true" class="font41 colorBlue">{{CreditAmount | format}}</span>
      <span v-else class="font41 colorBlue">{{CreditAmount | format}}</span>
    </div> -->
    <div class="LeftMidOne-ul ulTwo">
      <div>
        <span v-if="judegeMoney==true" class="font21">{{CreditAmount | format}}</span>
        <span v-else class="font21">{{CreditAmount | format}}</span>
        <span v-if="judegeMoney==true" class="font12">累计履约金额</span>
        <span v-else class="font12">累计履约金额</span>
          <span class="font12">(万元)</span>
      </div>
      <div>
        <span class="font21">{{UnPayAmount | format}}</span>
        <span class="font12">累计未支付金额</span>
          <span class="font12">(元)</span>
      </div>
      <div>
        <span class="font21">{{PayTimes | format}}</span>
        <span class="font12">平均付费次数</span>
          <span class="font12">&nbsp;</span>
      </div>
    </div>
  </div>
</template>

<script>
import store from '../store.js'
export default {
  name: 'LeftMidOne',
  mixins: [store],
  data () {
    return {
      HospitalCount:'',
      VisitCount:'',
      CreditCount:'',
      CreditAmount:'',
      UnPayAmount:'',
      PayTimes:'',
      time: 1000*60*10,
      time1: null,
      judegeMoney:true
    }
  },
  methods: {
    getData() {
      this.gethospitalListData(
          {
            "QueryCode": "330109000",
            "QueryType": "3"
          }
        ).then( res=>{
        let result = res.Data
        this.HospitalCount = result.HospitalCount
        this.VisitCount = result.VisitCount
        this.CreditCount = result.CreditCount
        if(parseInt(result.CreditAmount)>9999999999){
          this.judegeMoney = false;
          let CreditAmount = (result.CreditAmount /10000).toFixed(1)
          this.CreditAmount = CreditAmount.substring(0,CreditAmount.lastIndexOf('.'))
          console.log(this.CreditAmount);
        }else{
          this.judegeMoney = true;
          this.CreditAmount = parseInt(result.CreditAmount)
        }
        this.UnPayAmount = parseInt(result.UnPayAmount)
        this.PayTimes = result.PayTimes
      })
    }
  },
  created () {
      //先看病后付款请求数据
    this.getData()
    this.time1 = setInterval(()=>{
      this.getData()
    },this.time)
  },
  beforeDestroy(){
    // 清理定时器
    clearInterval(this.time1)
  }
}
</script>
