<
<template>
    <div class="mapTime">
        <div class="mapTime-bg">
            <div class="mapTime-bgsmall">
                <div>
                    <span class="font14">共计节约时间</span>
                    <span class="font41 colorBlue">{{sum_time | toFix}}</span>
                    <span class="font20 colorBlue">min</span>
                </div>
            </div>
        </div>
        <div class="mapTime-item">
            <div>
                <span class="font14">平均节约时间</span>
                <span class="font41 colorBlue">{{avg_time | toFix}}<span class="font21">S</span></span>
            </div>
            <div>
                <span class="font14">平均提升效率</span>
                <span class="font41 colorBlue">{{avg_time_efficient | toFix}}<span class="font21">%</span></span>
            </div>
        </div>
    </div>
</template>

<script>
    import store from '../store.js'

    export default {
        name: 'mapTime',
        mixins: [store],
        data() {
            return {
                sum_time: '',
                avg_time: '',
                avg_time_efficient: '',
                time1: null,
                time: 1000 * 60 * 30
            }
        },
        methods: {
            getData() {
                this.getSpecialTimeTotal().then(res => {
                    this.sum_time = res.data[0].sum_time
                    this.avg_time = res.data[0].avg_time
                    this.avg_time_efficient = res.data[0].avg_time_efficient
                })
            }
        },
        created() {
            this.getData()
            this.time1 = setInterval(() => {
                this.getData()
            }, this.time)
        },
        beforeDestroy() {
            // 清理定时器
            clearInterval(this.time1)
        }
    }
</script>
