<template>
    <div class="RightMid1">
        <div class="RightMid1-ul">
            <div>
                <span class="font21">{{waste | format|toFix2}}</span>
                <span class="font12">易腐垃圾量(吨)</span>
            </div>
            <div>
                <span class="font21">{{village | format}}</span>
                <span class="font12">分类小区(农村)</span>
            </div>
            <div>
                <span class="font21">{{family | format}}</span>
                <span class="font12">分类户数(万户)</span>
            </div>
        </div>
        <div class="RightMid1-charts">
            <span class="font14">{{month}}月垃圾分类参与率排名</span>
            <div class="RightMid1-list">
                <div>
                    <EchartsPie :options="echartsPie1" class="echarts"></EchartsPie>
                    <span class="font12">
            <span class="font12 setfontsize">{{noOneName}}</span>
          </span>
                </div>
                <div>
                    <EchartsPie :options="echartsPie2" class="echarts"></EchartsPie>
                    <span class="font12">
            <span class="font12 setfontsize">{{noTwoName}}</span>
          </span>
                </div>
                <div>
                    <EchartsPie :options="echartsPie3" class="echarts"></EchartsPie>
                    <span class="font12">
            <span class="font12 setfontsize">{{noThreeName}}</span>
          </span>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
    import EchartsPie from 'packages/cp-echarts'
    import store from '../store.js'

    function echartsPie(opts) {
        return {
            grid: {
                left: '2%',
                right: '2%',
                top: '2%',
                bottom: '2%'
            },
            // title: {
            //   show: true,
            //   text: opts.text + '%',
            //   x: 'center',
            //   y: '35%',
            //   textStyle: {
            //     fontSize: '100%',
            //     color: 'black',
            //     fontWeight: 'bold'
            //   }
            // },
            color: ['#3196FA', '#EFF7FF'],
            graphic: [{
                type: 'text',
                left: 'center',
                top: '30%',
                style: {
                    text: opts.number,
                    textAlign: 'center',
                    fill: '#fff',
                    font: 'italic 800 14px SourceSansPro-Regular',
                    width: 30,
                    height: 30,
                    fontWeight: 800,
                    color: 'blue'
                }
            },
                {　　　　　　　　　　　　　　　　//环形图中间添加文字
                    type: 'text',　　　　　　　　　　　　//通过不同top值可以设置上下显示
                    left: 'center',
                    top: '55%',
                    style: {
                        text: opts.text + '%',//（自己设置显示内容）
                        textAlign: 'center',
                        fill: '#00C6FF',　　　　　　　　//文字的颜色
                        width: 30,
                        height: 30,
                        fontSize: 16,
                        fontWeight: 'bold',
                        color: "black"
                    }
                }],
            series: [{
                type: 'pie',
                clockWise: true, // 顺时加载
                hoverAnimation: false, // 鼠标移入变大
                radius: ['80%', '92%'],
                center: ['50%', '50%'],
                label: {
                    show: false
                },
                data: opts.data
            }]
        }
    }

    export default {
        name: 'RightMidOne',
        mixins: [store],
        components: {
            EchartsPie
        },
        props: {},
        data() {
            return {
                echartsPie1: null,
                echartsPie2: null,
                echartsPie3: null,
                waste: '',
                village: '',
                family: '',
                result: null,
                time: 1000 * 60 * 60 * 1,
                time1: null,
                noOneName: '',
                noTwoName: '',
                noThreeName: '',
                month: '',
            }
        },
        methods: {
            getData() {
                this.getParkListData().then(res => {
                    console.log(res);
                    let result = res[0].data[0].value[0]
                    this.waste = result.waste
                    this.village = result.village
                    let family = ((result.family / 10000).toFixed(3))
                    this.family = family.substring(0, family.lastIndexOf('.') + 3)
                    this.result = result.classify
                    this.noOneName = result.classify[0].name
                    this.noTwoName = result.classify[1].name
                    this.noThreeName = result.classify[2].name
                    //插入列表
                    this.echartsPie1 = echartsPie({
                        text: this.result[0].rate>100?100:this.result[0].rate,
                        data: [
                            {
                                value: this.result[0].rate>100?100:this.result[0].rate,
                                name: ''
                            },
                            {
                                value: 100 - (this.result[0].rate>100?100:this.result[0].rate),
                                name: ''
                            }],
                        number: 'NO.1'
                    })

                    this.echartsPie2 = echartsPie({
                        text: this.result[1].rate>100?100:this.result[1].rate,
                        data: [
                            {
                                value: this.result[1].rate>100?100:this.result[1].rate,
                                name: ''
                            },
                            {
                                value: 100 - (this.result[1].rate>100?100:this.result[1].rate),
                                name: ''
                            }],
                        number: 'NO.2'
                    })

                    this.echartsPie3 = echartsPie({
                        text: this.result[2].rate>100?100:this.result[2].rate,
                        data: [
                            {
                                value: this.result[2].rate>100?100:this.result[2].rate,
                                name: ''
                            },
                            {
                                value: 100 - (this.result[2].rate>100?100:this.result[2].rate),
                                name: ''
                            }],
                        number: 'NO.3'
                    })
                }).catch((err) => {

                })
            },
            //获取当前月份方法
            getMonth() {
                var date = new Date;
                var year = date.getFullYear();
                var month = date.getMonth() + 1;
                this.month = month;
            }
        },
        created() {
            //获取当前月份
            this.getMonth();
            //获取垃圾分类数据
            this.getData()
            this.time1 = setInterval(() => {
                this.getData()
            }, this.time)
        },
        beforeDestroy() {
            // 清理定时器
            clearInterval(this.time1)
        }
    }
</script>
