<template>
  <div class="LeftMidTwo">
    <div class="setMonth">
        <span :class="{ active: this.isActive=='1' }" @click="change('1')">日</span>
        <span :class="{ active: this.isActive=='2' }" @click="change('2')">月</span>
        <span :class="{ active: this.isActive=='3' }" @click="change('3')">季</span>
    </div>
    <div>
      <Echarts :options="echarts" class="echarts"></Echarts>
    </div>
  </div>
</template>

<script>
import Echarts from 'packages/cp-echarts'
import store from '../store.js'
function echartsPie(opts){
  return {
     grid: {
      left: '15%',
      top: '28%',
      bottom: '15%'
    },
    xAxis: {
        name:'星级',
        nameTextStyle:{
            color:'#fff',
            fontWeight:400,
            fontSize:14
        },
        type: 'category',
        data: opts.x,
        splitLine:{show: false},//去除网格线
        axisLine: {
            lineStyle: {
                color: '#fff',//左边线的颜色
                width:'1'//坐标线的宽度
            }
        },
        axisLabel: {
            textStyle: {
                color: '#fff',//坐标值得具体的颜色

            }
        }
    },
    yAxis: [{
      name:'出租率',
       nameTextStyle:{
        color:'#fff',
        fontWeight:400,
        fontSize:14
      },
        splitLine:{show: false},//去除网格线
        axisLine: {
            lineStyle: {
                color: '#fff',//左边线的颜色
                width:'1'//坐标线的宽度
            }
        },
      type: 'value',
      axisLabel: {
        textStyle: {
          color: '#fff',
          fontSize: '50%',
        },
        interval: 0,
        formatter: '{value} %'
      }, 
      min:0,
      max: 100,
      maxInterval:25,
      splitNumber:4
    }],
    series: [{
        barWidth: 70, // 柱图宽度
        barMaxWidth: 70, // 最大宽度
        data: opts.y,
        type: 'bar',
        itemStyle: {
            normal: {
                color: new Echarts.graphic.LinearGradient(
                    0, 0, 0, 1,
                    [
                        {offset: 0, color: '#00C6FF'},
                        {offset: .7, color: '#0392FF'},
                        {offset: 1, color: '#0392FF'}
                    ]
                )
            }
        }
    }]
  }
}
export default {
  name: 'LeftMidTwo',
  mixins: [store],
  components: {
    Echarts
  },
  data () {
    return {
     echarts:null,
     isActive:'1'
    }
  },
  methods: {
    change(num){
      this.isActive = num;
      if(num == '1'){
        this.echarts = echartsPie({
          x:['3星','4星','5星'],
          y:['20','50','60']
        })
      }else if(num == '2'){
        this.echarts = echartsPie({
          x:['3星','4星','5星'],
          y:['50','70','20']
        })
      }else{
        this.echarts = echartsPie({
          x:['3星','4星','5星'],
          y:['10','60','60']
        })
      }
    }
  },
  created () {
   this.echarts = echartsPie({
     x:['3星','4星','5星'],
     y:['20','50','60']
   })
  },
  beforeDestroy(){
   
  }
}
</script>
<style lang="scss" scoped>
  .LeftMidTwo {
    width: 100%;
    height: vh(177);
    position: relative;
    /*background-color: #fff;*/
    margin-bottom: vh(10);
    padding: 0 vh(10);
  }
  .setMonth {
    position: absolute;
    right: vwMax(33);
    top: vh(5);
  }
  .setMonth span{
    position: relative;
    z-index: 88;
    display: inline-block;
    margin-right: vh(20);
    cursor: pointer;
    font-size:vh(14);
    color: #fff;
  }
  .setMonth .active{
    width:vwMax(56);
    height:vh(28);
    line-height: vh(28);
    text-align: center;
    background:rgba(68,235,255,0.2);
    border-radius:4px;
    border:1px solid rgba(68,235,255,1);
  }
  .echarts{
      width: vh(550);
      height: vh(200);
      /*margin-left: vwMax(5);*/
  }
</style>
