function scaling(w, h, sw, sh) {
    var containerRatio = sw / sh
    var imgRatio = w / h
    var top = 0
    var left = 0
    var s = 1
    if (imgRatio > containerRatio) {
        s = sw / w
        w = sw
        h = sw / imgRatio
    } else if (imgRatio < containerRatio) {
        s = sh / h
        h = sh
        w = sh * imgRatio
    } else {
        s = sw / w
        w = sw
        h = sh
    }
    if (s > 1) {
        top = 1080 * (s - 1)
        left = 1920 * (s - 1)-(w-sw)
    } else {
        top = -1080 * (1 - s)
        left = -1920 * (1 - s)-(w-sw)
    }
    return {width: w, height: h, scale: s, top, left}
}

export default scaling
