<script>
    import {parseTime} from '@/utils'
    import request from '@/utils/request'

    const timeSpan = 1000

    export default {
        name: "PageHeader",
        data() {
            return {
                time: '',
                date: '',
                weekDay: '',
                sTM: null,

            }
        },
        methods: {
            back(){
                this.$router.push({path:'/'})
            },
            show() {
                const weekdays = ["周日", "周一", "周二", "周三", "周四", "周五", "周六"]
                this.sTM = setInterval(() => {
                    const currentDate = new Date()
                    const datetime = parseTime(currentDate, 'yyyy.mm.dd hh:ii:ss')
                    this.date = datetime.split(' ')[0]
                    this.time = datetime.split(' ')[1]
                    this.weekDay = weekdays[currentDate.getDay()]
                }, timeSpan)
            },
            getWeather() {
                // request({
                //   url: 'http://t.weather.sojson.com/api/weather/city/101210101',
                //   method: 'get',
                // }).then((res)=>{
                //   console.log(res)
                // })
            }
        },
        mounted() {
            this.show()
            this.getWeather()
        },
        destroyed() {
            this.sTM && clearInterval(this.sTM)
        }
    }
</script>
