<template>
    <div class="RightTop">
        <div class="RightTop-top">
            <div class="RightTop-echarts">
                <EchartsPie :options="echartsPie" style="height:140px"></EchartsPie>
                <!--        <span class="font12">设备在线率</span>-->
            </div>
            <div class="RightTop-top-right">
                <div class="top">
                    <div>
                        <span class="font21">{{totalNumber | format}}</span>
                        <span class="font12">总人口</span>
                    </div>
                    <div>
                        <span class="font21">{{exitNumber | format}}</span>
                        <span class="font12">出入人次</span>
                    </div>
                    <div>
                        <span class="font21">{{trainNumber | format}}</span>
                        <span class="font12">出入车次</span>
                    </div>
                </div>
                <div class="bottom">
                    <div class="RightTop-btn">
                        <div class="RightTop-btnle">
                            <span class="font12 colorBlue"><img src="../../../../assets/icons/icon-per.png"
                                                                class="RightTop-icon">安心防护中</span>
                        </div>
                        <div class="RightTop-btnri">
                            <span class="font14">{{stranger | format}}  </span>
                            <span class="font10">位陌生人</span>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import EchartsPie from 'packages/cp-echarts'
    import store from '../store.js'

    function echartsPie(opts) {
        return {
            tooltip: {
                formatter: "{a} <br/>{b} : {c}%"
            },
            series: [
                {
                    radius: "100%",
                    name: '业务指标',
                    type: 'gauge',
                    detail: {
                        formatter: '{value}%',
                        textStyle: {       // 其余属性默认使用全局文本样式，详见TEXTSTYLE
                            color: '#000',
                            fontWeight: 'bolder'
                        }
                    },
                    axisLine: {            // 坐标轴线
                        lineStyle: {
                            width: 5,     // 属性lineStyle控制线条样式
                            color: [[0.2, '#c23531'], [0.8, '#3196FA'], [1, '#1CAD52']]
                        }
                    },
                    pointer: { //指针样式
                        length: '45%',
                        width: 3
                    },
                    axisTick: {            // 坐标轴小标记
                        length: 5,        // 属性length控制线长
                        lineStyle: {       // 属性lineStyle控制线条样式
                            color: 'auto'
                        }
                    },
                    splitLine: {           // 分隔线
                        length: 8,         // 属性length控制线长
                        lineStyle: {       // 属性lineStyle（详见lineStyle）控制线条样式
                            color: 'auto'
                        }
                    },
                    detail: {
                        // 其余属性默认使用全局文本样式，详见TEXTSTYLE
                        fontWeight: 'bolder',
                        fontSize: 18,
                    },
                    data: opts.data
                }
            ]
        }
    }

    export default {
        name: 'RightTop',
        mixins: [store],
        components: {
            EchartsPie
        },
        data() {
            return {
                echartsPie: null,
                onlineRate: '',
                totalNumber: '',
                exitNumber: '',
                trainNumber: '',
                stranger: '',
                timmer: null,
            }
        },
        methods: {
            getData() {
                this.getZaHhData().then(res => {
                    console.log(res)
                    let result = res[0].data[0].value[0]
                    this.onlineRate = result.onlineRate
                    this.totalNumber = result.totalNumber
                    this.exitNumber = result.exitNumber
                    this.trainNumber = result.trainNumber
                    this.stranger = result.stranger
                    this.echartsPie = echartsPie({
                        data: [{value: this.onlineRate}]
                    })
                }).catch((err) => {

                })
            }
        },
        created() {
            this.getData()
            this.timmer = setInterval(() => {
                this.getData()
            }, 1000 * 60 * 10)

        },
        beforeDestroy() {
            // 清理定时器
            clearInterval(this.timmer)
        }
    }
</script>
