<!--            </div>-->
<!--        </div>-->
        <ul class="Center-bottom">
            <li :class="{active: item.status===51}" v-for="(item, index) in option2" :key="index">
                <div class="box1">
                    <span class="font14">{{item.deviceID}}</span>
                    <span class="font10 colorBlue">120救援</span>
                </div>
                <div class="box2">
                    <span class="one font10">{{item.event.destinations[0].address | substrStr}}</span>
                    <span class="two font10"
                          :class="{colorYellow: item.status === 0, colorBlue: item.status === 70, colorRed: item.status === 100}">{{item.status | checkStatus}}</span>
                </div>
            </li>
        </ul>
