</template>

<script>
    import store from '../store.js'

    export default {
        name: 'Center',
        mixins: [store],
        props: {},
        data() {
            return {
                time5: null,
                time4: null,
                time6: null,
                ccl: '',
                hjl: '',
                jzbr: '',
                vhctotal: '',
                //信号灯在线 视频在线 护航车辆数
                time1: 1000 * 60 * 60 * 4,
                //呼救量 出车辆 救治病人数
                time2: 1000 * 60 * 30,
                time12: null,
                xhj: '',
                sp: '',
                option: [],
                option2: [],
            }
        },
        methods: {
            //获取视频在线
            getVedio() {
                this.getToyxgaData().then(res => {
                    this.xhj = res.data[0].xhj;
                    this.sp = res.data[0].sp;
                })
            },
            getData5() {
                this.getListByTime().then(res => {
                    this.ccl = res.data[0].ccl
                    this.hjl = res.data[0].hjl
                    this.jzbr = res.data[0].jzbr
                })
            },
            getData4() {
                this.getAllFourData().then(res => {
                    this.vhctotal = res.data[0].vhctotal
                })
            },
            getData6() {
                this.getStatusStatistic({
                    appkey: "qVPsOwIExxvSe2BIoIGRt1U4GPntsCxn"
                }).then(res => {
                    for (let i = 1; i < 7; i++) {
                        this.option.push(res.data[i])
                    }
                    this.option2 = this.option
                })
            }
        },
        created() {
            //获取视频在线
            this.getVedio()
            this.time12 = setInterval(() => {
                this.getVedio()
            }, this.time1)
            //呼救量 出车辆 救治病人数
            this.getData5()
            this.time5 = setInterval(() => {
                this.getData5()
            }, this.time2)

            this.getData4()
            this.time4 = setInterval(() => {
                this.getData4()
            }, this.time1)

            this.getData6()
            this.time6 = setInterval(() => {
                this.option = []
                this.getData6()
            }, this.time2)
        },
        beforeDestroy() {
            // 清理定时器
            clearInterval(this.time4)
            clearInterval(this.time5)
            clearInterval(this.time12)
            clearInterval(this.time6)
        }
    }
</script>
