<template>
  <div class="RightBottom">
    <div class="RightBottom-ul">
      <div>
        <span class="font21">{{businessNumbe | format}}</span>
        <span class="font12">产废企业数</span>
      </div>
      <div>
        <span class="font21">{{solidNumber | format}}</span>
        <span class="font12">固废转运联单数</span>
      </div>
      <div>
        <span class="font21">{{transfer | format}}</span>
        <span class="font12">固废转移数(吨)</span>
      </div>
    </div>
  </div>
</template>

<script>

import store from '../store.js'
export default {
  name: 'RightBottom',
  mixins: [store],
  data () {
    return {
      businessNumbe: '',
      solidNumber: '',
      transfer: '',
      time1: null,
      time: 1000*60*60*4,
    }
  },
  methods: {
    getData() {
      this.getHuanBaoData().then( res => {
        let result = res[0].data[0].value[0]
        this.businessNumbe = result.businessNumber
        this.solidNumber = result.solidNumber
        this.transfer = parseInt(result.transfer)
      }).catch((err) => {

      })
    }
  },
  created () {
    this.getData()
    this.time1 = setInterval(()=>{
      this.getData()
    },this.time)
  },
  beforeDestroy(){
    // 清理定时器
    clearInterval(this.time1)
  }
}
</script>
