<template>
  <div class="Panel" @click="handleClick">
    <slot name="title"  v-if="title || $slots.title">
      <div class="header_h1">{{title}}</div>
    </slot>
    <slot></slot>
  </div>
</template>
<script>
export default {
  name: "CpPanel",
  props: {
    title: {
      type: String,
      default: ""
    }
  },
  methods: {
    handleClick() {
      this.$emit('click')
    }
  }
}
</script>
