<template>
    <div class="RightMid2">
        <div class="top">
            <div class="RightMid2-top">
                <div class="RightMid2-left"><span class="font14 colorBlue">今日实时警情</span></div>
                <div class="RightMid2-ri">
                    <div>
                        <span class="font21">{{ewTotal | format}}</span>
                        <span class="font14">预警数</span>
                    </div>
                    <div class="two">
                        <span class="font21">{{fireTotal | format}}</span>
                        <span class="font14">成灾火警数</span>
                    </div>
                </div>
            </div>
            <div class="RightMid2-middle">
                <div class="RightMid2-left"><span class="font14 colorBlue">趋势火灾报警</span></div>
                <div class="RightMid2--ri">
                    <div class="echarts">
                        <Echarts :options="echarts"></Echarts>
                    </div>
                </div>
            </div>
        </div>

        <div class="RightMid2-bottom">
            <span class="font14 title">区域预警态势</span>
            <ul class="RightMid2-ul">
                <li>
                    <span class="font12 blod" style="font-style: italic;">NO.1 </span>
                    <i v-if="this.oneStaus==1" class="up"></i>
                    <i v-else-if="this.oneStaus==0" class="shuip"></i>
                    <i v-else-if="this.oneStaus==2" class="down"></i>
                    <div class="RightMid2-item">
                        <div>
                            <span class="font12 text">{{oneRoad}}</span>
                            <span class="font12 xfTimes">{{oneNo| format}}次</span>
                        </div>
                        <div class="RightMid2-line">
                            <span style="width:80%"></span>
                        </div>
                    </div>
                </li>
                <li>
                    <span class="font12 blod" style="font-style: italic;">NO.2 </span>
                    <i v-if="this.twoStatus==1" class="up"></i>
                    <i v-else-if="this.twoStatus==0" class="shuip"></i>
                    <i v-else-if="this.twoStatus==2" class="down"></i>
                    <div class="RightMid2-item">
                        <div>
                            <span class="font12 text">{{twoRoad}}</span>
                            <span class="font12 xfTimes">{{twoNo | format}}次</span>
                        </div>
                        <div class="RightMid2-line">
                            <span style="width:70%"></span>
                        </div>
                    </div>
                </li>
                <li>
                    <span class="font12 blod" style="font-style: italic;">NO.3 </span>
                    <i v-if="this.threeStatus==1" class="up"></i>
                    <i v-else-if="this.threeStatus==0" class="shuip"></i>
                    <i v-else-if="this.threeStatus==2" class="down"></i>
                    <div class="RightMid2-item">
                        <div>
                            <span class="font12 text">{{threeRoad}}</span>
                            <span class="font12 xfTimes">{{threeNo | format}}次</span>
                        </div>
                        <div class="RightMid2-line">
                            <span style="width:60%"></span>
                        </div>
                    </div>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
    import Echarts from 'packages/cp-echarts'
    import store from '../store.js'

    function echartsPie(opts) {
        return {
            grid: {
                left: '15%',
                right: '10px',
                top: '10%',
                bottom: '22%'
            },
            xAxis: {
                type: 'category',
                boundaryGap: false,
                data: opts.xData,
                axisLine: {
                    lineStyle: {
                        color: '#3196FA',
                    }
                }
            },
            yAxis: {
                type: 'value',
                splitLine: {
                    show: false
                },
                minInterval: 20,
                axisLine: {
                    lineStyle: {
                        color: '#3196FA',
                    }
                }
            },
            series: [{
                data: opts.yData,
                type: 'line',
                areaStyle: {
                    color:'rgb(0,198,255,0.2)'
                },
                color: ['#3196FA'],
                smooth: true
            }]
        }
    }

    export default {
        name: 'RightMidTwo',
        mixins: [store],
        components: {
            Echarts
        },
        props: {},
        data() {
            return {
                echarts: null,
                result: null,
                xData: [],
                yData: [],
                ewTotal: '',
                fireTotal: '',
                time: 1000 * 60 * 10,
                //1小时垃圾分了参与率等延时
                time0: 1000 * 60 * 60 * 1,
                time1: null,
                time2: null,
                time3: null,
                oneNo: '',
                oneRoad: '',
                twoNo: '',
                twoRoad: '',
                threeNo: '',
                threeRoad: '',
                oneStaus: '',
                twoStatus: '',
                threeStatus: ''
            }
        },
        methods: {
            getDataJqJd() {
                this.getJqJdData().then(res => {
                    let result = res[0].data[0].value[0]
                    this.oneNo = result.data[0].number
                    this.oneRoad = result.data[0].road
                    this.oneStaus = result.data[0].status
                    this.twoNo = result.data[1].number
                    this.twoRoad = result.data[1].road
                    this.twoStatus = result.data[1].status
                    this.threeNo = result.data[2].number
                    this.threeRoad = result.data[2].road
                    this.threeStatus = result.data[2].status
                }).catch((err) => {
                })
            },
            getDataMap() {
                this.getMapData().then(res => {
                    let result = res[0].data[0].value[0]
                    result.data.map((item) => {
                        this.xData.push(item.label)
                        this.yData.push(item.value)
                    })
                }).catch((err) => {
                })
            },
            getDataJq() {
                this.getJqData().then(res => {
                    let result = res[0].data[0].value[0]
                    this.ewTotal = result.ewTotal
                    this.fireTotal = result.fireTotal
                }).catch((err) => {

                })
            }
        },
        created() {
            this.getDataJqJd()
            this.getDataMap()
            this.getDataJq()

            this.time1 = setInterval(() => {
                this.getDataJqJd()
            }, this.time0)

            this.time2 = setInterval(() => {
                this.xData = []
                this.yData = []
                this.getDataMap()
            }, this.time0)

            this.echarts = echartsPie({
                xData: this.xData,
                yData: this.yData
            })
            this.time3 = setInterval(() => {
                this.getDataJq()
            }, this.time)
        },
        beforeDestroy() {
            // 清理定时器
            clearInterval(this.time1)
            clearInterval(this.time2)
            clearInterval(this.time3)
        }
    }
</script>
