const ColorLevel = [
  "#2a3f00",
  "#1c5f1f",
  "#469e4a",
  "#6abb6d",
  "#fff07d",
  "#fcc302",
  "#ff8f01",
  "#fc6b4e",
  "#f11908",
  "#7f0101"
]
// 判断难度值，返回对应颜色
function switchColor(value) {
  const val = parseInt(Number(value) * 10)
  return ColorLevel[Math.min(9,val)]
}

export default {
  mixins: [store],
  data() {
    return {
      // 数据
      mapAreaTime: null,
      info: '',
      mapArea_grid: [],
      mapArea_marker: [],
      mapArea_polygon: [],
      blocks: [], // 区块数据
      historyBlocks:  {}// 历史区块数据
    }
  },
  methods: {

    // 获取时间段 blocks 数据
    async initMapArea(){
      // 初始化地图
      // 初始化 盲点 marker
      this.initMapMarker()
    },
    initMapMarker() {
      //请求标点然后地图标点
      this.getMapPointData().then( res=>{
        this.blindPointList = res.data;
        if (this.mapArea_marker.length) return
        // 因为 requestScotomas 定时调用，无需重复请求
        if (this.blindPointList.length === 0) this.blindPointList = lanLngs
        this.mapArea_marker = this.blindPointList.map((point) =>{
          const latLng = { lat: point.lat, lng: point.lng }
          let popup='';
          if(point.type=='4'||point.type=='5'){
            popup = L.popup({
              autoClose: false,
              minWidth: 200,
              offset:L.point(4, -4),
              className: "blindDot_marker2",
              closeButton:false
            })
              .setLatLng(latLng)
              .setContent(`<div class="blindDot_marker"><div class="map-padding">${point.toponymy}</div>
                  <div class="myline"></div>
                  <div class="setimage">
                    <img style="height: 34px;vertical-align: middle;margin-right: 5px;margin-bottom: 16px;" src='${jijuicon}' alt="急救图"/>
                    <img style="height: 8px; height: 12px; vertical-align: top;margin-top: 10px;" src='${iconRan}'>
                  </div>           
              </div>`).openOn(this.mapArea)
          }else {
            popup = L.popup({
              autoClose: false,
              minWidth: 200,
              closeButton:false,
              offset:L.point(-30,48),
              className: "setTb"
            })
              .setLatLng(latLng)
              .setContent(`<div class="blindDot_markerTb"><div class="map-padding">${point.toponymy || ''}</div></div>`).openOn(this.mapArea)
          }
          if(point.type=='1'){
            return L.marker(latLng, {icon: icon1}).addTo(this.mapArea).bindPopup(popup)
          }else if(point.type=='2'){
            return L.marker(latLng, {icon: icon2}).addTo(this.mapArea).bindPopup(popup)
          }else if(point.type=='3'){
            return L.marker(latLng, {icon: icon4}).addTo(this.mapArea).bindPopup(popup)
          }else if(point.type=='4'){
            return L.marker(latLng, {icon: icon3}).addTo(this.mapArea).bindPopup(popup)
          }else if(point.type=='5'){
            return L.marker(latLng, {icon: icon3}).addTo(this.mapArea).bindPopup(popup)
          }
        })
      })
    },
    handleMapHeatEnter2() {
      // 地图打点
      this.initMapArea()
    }
  },
  mounted() {
    this.handleMapHeatEnter2()
  }
}
